import React from "react";
import styled from "styled-components";
import { Link } from "gatsby";

import Layout from "../layouts";

import NotFoundBg from "../../images/notfound-bg.png";
import NotFoundBtn from "../../images/notfound-btn.png";
import { breakpoints } from "../theme/Constants";
import { NavigationHeader } from "../components/Navigation";

const Header = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  padding: 24px 3vw;

  @media (max-width: ${breakpoints.sm}) {
    padding: 12px 16px;
  }
`;

const Wrapper = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #f3f3f3;
  margin-top: 16vh;
`;

const ModalDiv = styled.div`
  position: relative;
`;

const BgImg = styled.img`
  width: 540px;
  height: auto;

  @media (max-width: ${breakpoints.sm}) {
    width: 100%;
  }
`;

const BtnLink = styled(Link)`
  position: absolute;
  left: 50%;
  bottom: 32px;
  transform: translateX(-50%);

  @media (max-width: ${breakpoints.sm}) {
    bottom: 20px;
  }
`;

const BtnImg = styled.img`
  width: 203px;
  height: auto;

  @media (max-width: ${breakpoints.sm}) {
    width: 37vw;
  }
`;

const NotFound: React.FC = () => (
  <Layout showNavigation={false} showFooter={false}>
    <Header>
      <NavigationHeader />
    </Header>
    <Wrapper>
      <ModalDiv>
        <BgImg src={NotFoundBg} alt="404-bg" />
        <BtnLink to="/">
          <BtnImg src={NotFoundBtn} alt="404-btn" />
        </BtnLink>
      </ModalDiv>
    </Wrapper>
  </Layout>
);

export default NotFound;
